body {
  background: coral;
  }
* {
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 700;
  }
.title{
  margin-bottom: 30px;
  font-size: 30px;
}
.app {
  height: auto;
  margin-top: 48px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: column;
  color: #793535;
  margin-bottom: 30px;
  }
.todo-list {
  margin-top: 16px;
  height: 48px;
  width: 550px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  color: #793535;
  font-size: 20px;
  }
li {
  list-style: none;
  overflow-wrap: anywhere;
  height: auto;
  width: 176px;
  }
input{
  color: darkcyan;
  border: 4px solid;
  border-radius: 10px;
  background-color: antiquewhite;
  font-size: 20px;
}
button {
  padding: 0 8px;
  margin: 0 4px;
  border-radius: 8px;
  color: darkcyan;
  background-color: sandybrown;
  cursor: pointer;
  font-size: 13px;
  }
button:hover {
  color: #793535;
  background-color: sandybrown;
}